@tailwind base;
@tailwind components;
@tailwind utilities;

/* Fonts */
@font-face {
  font-family: 'PPEditorialNew';
  font-weight: normal;
  font-style: normal;
  src: local('PPEditorialNew'), url('./fonts/PPEditorialNew/PPEditorialNew-Ultralight.otf') format('opentype');
  /* other formats include: 'woff2', 'truetype, 'opentype',
                            'embedded-opentype', and 'svg' */
}


@font-face {
  font-family: 'PPEditorialNew';
  font-weight: bold;
  font-style: normal;
  src: local('PPEditorialNew'), url('./fonts/PPEditorialNew/PPEditorialNew-Ultrabold.otf') format('opentype');
  /* other formats include: 'woff2', 'truetype, 'opentype',
                            'embedded-opentype', and 'svg' */
}

@font-face {
  font-family: 'PPEditorialNew';
  font-weight: normal;
  font-style: italic;
  src: local('PPEditorialNew'), url('./fonts/PPEditorialNew/PPEditorialNew-UltralightItalic.otf') format('opentype');
  /* other formats include: 'woff2', 'truetype, 'opentype',
                            'embedded-opentype', and 'svg' */
}

@font-face {
  font-family: 'PPEditorialNew';
  font-weight: bold;
  font-style: italic;
  src: local('PPEditorialNew'), url('./fonts/PPEditorialNew/PPEditorialNew-UltraboldItalic.otf') format('opentype');
  /* other formats include: 'woff2', 'truetype, 'opentype',
                            'embedded-opentype', and 'svg' */
}


body {
  margin: 0;
  font-family: PPEditorialNew, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


